/** @define Badge */

/**
 * Badges are useful to label, highlight or classify content.
 */

:root {
    --Badge-min-height: 0.875rem;
    --Badge-min-width: 0.875rem;

    --Badge-background-color: var(--background-color--secondary);
    --Badge-border-width: 0;
    --Badge-border-radius: var(--border-radius--default);
    --Badge--pill-border-radius: 1.5em;

    --Badge-font-family: inherit;
    --Badge-text-color: var(--text-color--default);

    --Badge-font-size: var(--font-size--regular);
    --Badge-padding: 0.25rem 0.75rem;
    --Badge--small-font-size: var(--font-size--small);
    --Badge--small-padding: 0.125rem 0.5rem;
    --Badge--large-font-size: var(--font-size--regular);
    --Badge--large-padding: 0.5rem 1rem;

    --Badge--info-background-color: var(--color-info);
    --Badge--info-text-color: var(--text-color--onDark);
    --Badge--success-background-color: var(--color-success);
    --Badge--success-text-color: var(--text-color--onDark);
    --Badge--warning-background-color: var(--color-warning);
    --Badge--warning-text-color: var(--text-color--onLight);
    --Badge--error-background-color: var(--color-error);
    --Badge--error-text-color: var(--text-color--onDark);
}

.Badge {
    position: relative;

    display: inline-block;
    vertical-align: middle;

    box-sizing: border-box;
    margin: 0;
    padding: var(--Badge-padding);

    min-height: var(--Badge-min-height);
    min-width: var(--Badge-min-width);

    background: var(--Badge-background-color);
    border: var(--Badge-border-width);
    border-radius: var(--Badge-border-radius);
    white-space: nowrap;

    font-family: var(--Badge-font-family);
    font-size: var(--Badge-font-size);
    color: var(--Badge-text-color);

    line-height: normal;
    text-align: center;
    text-decoration: none;
    text-transform: none;
}

.Badge.is-empty {
    min-height: 1em;
    min-width: 1em;
}

/* Types
   ========================================================================== */

.Badge--pill {
    border-radius: var(--Badge--pill-border-radius);
}

/* Indicators
   ========================================================================== */

.Badge {
    &--info {
        background-color: var(--Badge--info-background-color);
        color: var(--Badge--info-text-color);
    }

    &--success {
        background-color: var(--Badge--success-background-color);
        color: var(--Badge--success-text-color);
    }

    &--warning {
        background-color: var(--Badge--warning-background-color);
        color: var(--Badge--warning-text-color);
    }

    &--error {
        background-color: var(--Badge--error-background-color);
        color: var(--Badge--error-text-color);
    }
}

/* Sizes
   ========================================================================== */

.Badge {
    &--small {
        padding: var(--Badge--small-padding);
        font-size: var(--Badge--small-font-size);
    }

    &--large {
        padding: var(--Badge--large-padding);
        font-size: var(--Badge--large-font-size);
    }
}

.Badge:empty {
    padding: 0;

    min-height: var(--Badge-min-height);
    min-width: var(--Badge-min-width);

    border-radius: 1em;
}
