/* stylelint-disable swissquote/no-type-outside-scope */

/**
 * Set up a decent box model on all the elements from the DOM.
 */

:root {
    --Global-font-size: var(--font-size--regular);
    --Global-text-color: var(--text-color--default);
    --Global-background-color: var(--background-color--default);

    --Global-link-color: var(--text-color--default);
    --Global-link-active-color: var(--text-color--primary);

    --Global-hr-background-color: #cfd7de;
    --Global-mark-background-color: #fcf3d0;

    --Global-webkit-tap-highlight-color: var(--color-transparent);
}

*,
*:before,
*:after {
    box-sizing: border-box;
}

html {
    font: 100%/1.5 Helvetica, Arial, sans-serif;
    background: var(--Global-background-color);
    color: var(--Global-text-color);
    -webkit-tap-highlight-color: var(--Global-webkit-tap-highlight-color);
}

body {
    font-size: var(--Global-font-size);
    background: transparent;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
fieldset {
    margin: 0;
    padding: 0;
}

figure {
    margin: 0;
}

abbr[title],
fieldset,
hr {
    border: 0;
}

hr {
    padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: inherit;
    font-size: 1.1em;
}

a {
    color: var(--Global-link-color);
    text-decoration: underline;

    transition: color 0.1s linear;

    &:hover,
    &:focus {
        color: var(--Global-link-active-color);
        text-decoration: underline;
    }
}

a:active {
    color: var(--Global-link-active-color);
}

a,
button {
    cursor: pointer;
}

b,
strong,
dfn {
    font-weight: 700;
}

mark {
    background-color: var(--Global-mark-background-color);
}

abbr[title] {
    cursor: help;
}

input[type=search] {
    box-sizing: border-box;
}

caption,
th,
td {
    text-align: left;
}

hr {
    display: block;
    margin: 1rem 0;
    border: 0;
    background: var(--Global-hr-background-color);
    overflow: hidden;
    height: 1px;
    clear: both;
}

audio,
canvas,
iframe,
img,
svg,
video {
    vertical-align: middle;
}

img {
    max-width: 100%;
    height: auto;
}

/**
 * Opinionated defaults
  */

/**
 * Remove most spacing between table cells (from Normalize.css 3.0.3).
 */

table {
    border-collapse: collapse;
    border-spacing: 0;
}

td,
th {
    padding: 0;
}

/**
 * Definition lists
 */

dt {
    font-weight: bold;
}

* + dt {
    margin-top: 1em;
}

dd {
    margin: 0;
    padding: 0;
}
