:root {
    --Navbar-font-size: var(--font-size--regular);
    --Navbar-background-color: var(--background-color--default);
    --Navbar-color: var(--text-color--default);
    --Navbar-padding-top: 1.25rem;
    --Navbar-padding-right: var(--space-size--md);
    --Navbar-padding-bottom: 1.25rem;
    --Navbar-padding-left: var(--space-size--md);

    --Navbar-box-shadow-color: var(--shadow-color--default);
    --Navbar--top-box-shadow-width: var(--shadow-size--8);
    --Navbar--bottom-box-shadow-width: var(--shadow-size--8-inverse);

    --Navbar--app--top-border-color: var(--border-color--primary);
    --Navbar--app--top-border-width: 4px;
    --Navbar--app--top-border-style: var(--border-style--default);

    --Navbar__item--spacing: var(--space-size--sm);
    --Navbar-z-index: var(--z-index--Navbar);
}

.Navbar {
    position: relative;
    z-index: var(--Navbar-z-index);
    margin: 0;
    padding: 0 var(--Navbar-padding-right) 0 var(--Navbar-padding-left);
    border-bottom: var(--Navbar--app--top-border-width) var(--Navbar--app--top-border-style) var(--Navbar--app--top-border-color);
    background-color: var(--Navbar-background-color);
    color: var(--Navbar-color);
    font-size: 0;
}

.Navbar {
    &--fixed {
        position: fixed;
    }

    &--top {
        top: 0;
        right: 0;
        left: 0;
    }

    &--bottom {
        right: 0;
        bottom: 0;
        left: 0;
        border-bottom: 0;
        border-top: var(--Navbar--app--top-border-width) var(--Navbar--app--top-border-style) var(--Navbar--app--top-border-color);
    }
}

.Navbar--app {
    border: 0;
    box-shadow: var(--Navbar--top-box-shadow-width) var(--Navbar-box-shadow-color);

    &.Navbar--top {
        box-shadow: var(--Navbar--top-box-shadow-width) var(--Navbar-box-shadow-color);
    }

    &.Navbar--bottom {
        box-shadow: var(--Navbar--bottom-box-shadow-width) var(--Navbar-box-shadow-color);
    }
}

.Navbar__item {
    margin: 0;
    display: inline-block;
    vertical-align: middle;
    padding: var(--Navbar-padding-top) 0 var(--Navbar-padding-bottom) 0;
    font-size: var(--Navbar-font-size);
}

.Navbar__section {
    position: absolute;
    top: 0;
}

.Navbar__section--right {
    right: var(--Navbar-padding-right);
}

[dir=rtl] .Navbar__section--right {
    right: auto;
    left: var(--Navbar-padding-left);
}

.Navbar__item + .Navbar__item,
.Navbar__item + .Navbar__section,
.Navbar__section + .Navbar__item {
    margin-left: var(--Navbar__item--spacing);
}

[dir=rtl] {
    .Navbar__item + .Navbar__item,
    .Navbar__item + .Navbar__section,
    .Navbar__section + .Navbar__item {
        margin-left: 0;
        margin-right: var(--Navbar__item--spacing);
    }
}
