:root {
    --Search-font-size: var(--font-size--regular);
}

.Search {
    position: relative;
}

.Search__clear,
.Search__action {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 10;
}

.Search__clear {
    display: none;
}

.Search {
    &.has-text .Search__clear {
        display: block;
    }

    &.has-action .Search__clear {
        right: 2.25rem;
    }

    &--small.has-action .Search__clear {
        right: 2.125rem;
    }

    &--large.has-action .Search__clear {
        right: 2.625rem;
    }
}

.Search {
    & .Input {
        padding-right: 1.75rem;
    }

    &.has-action .Input {
        padding-right: 4rem;
    }

    &--small.has-action .Input {
        padding-right: 3.75rem;
    }

    &--large.has-action .Input {
        padding-left: 0.5rem;
        padding-right: 4.5rem;
    }
}

.Search.is-disabled .Button.is-disabled {
    border: transparent;
    background: transparent;
}

.Search {
    &.has-action .Icon {
        width: 1.125rem;
        height: 1.125rem;
    }

    &--small.has-action .Icon {
        width: 1rem;
        height: 1rem;
    }

    &--large.has-action .Icon {
        width: 1.5rem;
        height: 1.5rem;
    }
}

.Search {
    & .Button {
        padding-right: 0.5rem;
        padding-left: 0.5rem;
        max-height: 32px;
    }

    & .Button--text {
        text-decoration: none;
    }

    &--small .Search__action .Button {
        max-height: 24px;
    }

    &--large .Button--large {
        padding: 0.875rem 0.5rem;
        max-height: 48px;
    }
}

[dir=rtl] .Search {
    & .Search__clear,
    & .Search__action {
        right: auto;
        left: 0;
    }

    &.has-action .Search__clear {
        right: auto;
        left: 2.25rem;
    }

    &--small.has-action .Search__clear {
        right: auto;
        left: 2.125rem;
    }

    &--large.has-action .Search__clear {
        right: auto;
        left: 2.625rem;
    }

    & .Input {
        padding-right: 0.5rem;
        padding-left: 1.75rem;
    }

    &.has-action .Input {
        padding-left: 4rem;
    }

    &--small.has-action .Input {
        padding-left: 3.75rem;
    }

    &--large.has-action .Input {
        padding-right: 0.5rem;
        padding-left: 4.5rem;
    }

}
