/** @define Metadata */

/**
 * A Metadata is used to emphasize the current value of an attribute.
 */

:root {
    /* Palette */
    --Metadata-text-color: var(--text-color--default);
    --Metadata-font-size: 2.25rem;

    --Metadata__label-text-color: var(--text-color--secondary);
    --Metadata__label-font-size: 0.35em;
    --Metadata__label-font-weight: 300;

    /* Indicators */
    --Metadata--info-text-color: var(--color-info);
    --Metadata--success-text-color: var(--color-success);
    --Metadata--warning-text-color: var(--color-warning);
    --Metadata--error-text-color: var(--color-error);

    --Metadata--xsmall-font-size: var(--font-size--xsmall);
    --Metadata--small-font-size: var(--font-size--small);
    --Metadata--medium-font-size: var(--font-size--base);
    --Metadata--large-font-size: var(--font-size--large);
    --Metadata--xlarge-font-size: var(--font-size--xlarge);
}

.Metadata {
    position: relative;
    display: inline-block;
    margin: 0;

    color: var(--Metadata-text-color);

    font-size: var(--Metadata-font-size);
}

.Metadata__label {
    padding: 0.25rem 0;
    text-transform: uppercase;
    font-size: var(--Metadata__label-font-size);
    color: var(--Metadata__label-text-color);
    font-weight: var(--Metadata__label-font-weight);
}

/* Styles
   ========================================================================== */

.Metadata--center {
    text-align: center;
}

.Metadata--right {
    text-align: right;
}

/* Not so semantic, but understandable */
[dir=rtl] .Metadata--right {
    text-align: left;
}

.Metadata--inline {
    .Metadata__value,
    .Metadata__label {
        display: inline-block;

        vertical-align: middle;
    }
}

.Metadata--inline .Metadata__label {
    padding: 0 0.25rem;
}

/* Indicators
   ========================================================================== */

.Metadata {
    &--info {
        color: var(--Metadata--info-text-color);
    }

    &--success {
        color: var(--Metadata--success-text-color);
    }

    &--warning {
        color: var(--Metadata--warning-text-color);
    }

    &--error {
        color: var(--Metadata--error-text-color);
    }
}

/* Sizes
   ========================================================================== */

.Metadata {
    &--xsmall {
        font-size: var(--Metadata--xsmall-font-size);
    }

    &--small {
        font-size: var(--Metadata--small-font-size);
    }

    &--medium {
        font-size: var(--Metadata--medium-font-size);
    }

    &--large {
        font-size: var(--Metadata--large-font-size);
    }

    &--xlarge {
        font-size: var(--Metadata--xlarge-font-size);
    }
}
