:root {
    --Heading--xsmall-font-size: var(--font-size--small);
    --Heading--small-font-size: var(--font-size--regular);
    --Heading--medium-font-size: var(--font-size--base);
    --Heading--large-font-size: var(--font-size--large);
    --Heading--xlarge-font-size: 1.25rem;
}

.Heading {
    font-weight: 700;
    font-size: var(--Heading--medium-font-size);
}

/* Sizes
   ========================================================================== */

.Heading {
    &--xsmall {
        font-size: var(--Heading--xsmall-font-size);
    }

    &--small {
        font-size: var(--Heading--small-font-size);
    }

    &--medium {
        font-size: var(--Heading--medium-font-size);
    }

    &--large {
        font-size: var(--Heading--large-font-size);
    }

    &--xlarge {
        font-size: var(--Heading--xlarge-font-size);
    }
}
