/** @define Breadcrumb */

/**
 * Breadcrumbs are used to show hierarchy between content
 * and help the user navigate back to the parent.
 */

:root {
    /* Palette */
    --Breadcrumb-font-size: var(--font-size--regular);
    --Breadcrumb-text-color: var(--text-color--default);
    --Breadcrumb__separator-text-color: var(--text-color--default);
    --Breadcrumb__currentItem-text-color: var(--text-color--primary);
}

.Breadcrumb {
    position: relative;
    font-size: 0;
    color: var(--Breadcrumb-text-color);
}

.Breadcrumb__items {
    list-style: none;
    margin: 0;
    padding: 0;
}

.Breadcrumb__item {
    font-size: var(--Breadcrumb-font-size);
    display: inline-block;
    color: inherit;
}

.Breadcrumb__item > a {
    text-decoration: none;
    color: inherit;
}

.Breadcrumb__item + .Breadcrumb__item {
    margin-left: 1rem;
}

.Breadcrumb__item + .Breadcrumb__item:before {
    position: absolute;
    margin-left: -0.75rem;

    color: var(--Breadcrumb__separator-text-color);

    content: "\003E";
}

.Breadcrumb__item.is-current {
    color: var(--Breadcrumb__currentItem-text-color);
}

[dir=rtl] .Breadcrumb__item + .Breadcrumb__item:before {
    margin-left: 0;
    margin-right: -0.75rem;
}

[dir=rtl] .Breadcrumb__item + .Breadcrumb__item {
    margin-left: 0;
    margin-right: 1rem;
}
