:root {
    --Popup__content-hover-border-color: var(--Border-color);
    --Popup__trigger-height: 2em;
    --Popup__trigger-border-color: var(--Border-color);
    --Popup__trigger-background-color: var(--Background-color-main);
    --Popup__triger-hover__content-border-color: var(--Border-color);
}

.Popup {
    position: absolute;
    max-width: 100%;
    background: #fff;
    border: 1px solid #e3e8ec;
    box-shadow: 0 0 1px 1px rgba(233, 239, 243, 0.75);
    border-radius: 2px;
    vertical-align: top;
}

.Popup__header {
    position: relative;
    padding: 0.5em 1em;
    border-bottom: 1px solid #e3e8ec;
}

.Popup__content {
    position: relative;
    padding: 0.75em 1em;
}

.Popup__arrow:after,
.Popup__arrow {
    position: absolute;
    width: 0;
    height: 0;
    border-color: #999;
    border-color: rgba(0, 0, 0, 0.15);
    border-style: solid;
}

.Popup__arrow:after {
    content: "";
    display: block;
    border-color: white;
}

.Popup[data-placement^="top"] {
    margin-bottom: 11px;
}

.Popup[data-placement^="top"] .Popup__arrow {
    border-width: 11px 11px 0 11px;
    border-bottom-color: transparent;
    border-left-color: transparent;
    border-right-color: transparent;
    left: calc(50% - 11px);
    margin-top: 0;
    margin-bottom: 0;
    bottom: -11px;
}

.Popup[data-placement^="top"] .Popup__arrow:after {
    border-width: 10px 10px 0 10px;
    border-bottom-color: transparent;
    border-left-color: transparent;
    border-right-color: transparent;
    left: calc(50% - 10px);
    margin-top: 0;
    margin-bottom: 0;
    bottom: 1px;
}

.Popup[data-placement^="bottom"] {
    margin-top: 10px;
}

.Popup[data-placement^="bottom"] .Popup__arrow {
    border-width: 0 11px 11px 11px;
    border-top-color: transparent;
    border-left-color: transparent;
    border-right-color: transparent;
    left: calc(50% - 11px);
    margin-top: 0;
    margin-bottom: 0;
    top: -11px;
}

.Popup[data-placement^="bottom"] .Popup__arrow:after {
    border-width: 0 10px 10px 10px;
    border-top-color: transparent;
    border-left-color: transparent;
    border-right-color: transparent;
    left: calc(50% - 10px);
    margin-top: 0;
    margin-bottom: 0;
    top: 1px;
}

.Popup[data-placement^="right"] {
    margin-left: 10px;
}

.Popup[data-placement^="right"] .Popup__arrow {
    border-width: 11px 11px 11px 0;
    border-left-color: transparent;
    border-top-color: transparent;
    border-bottom-color: transparent;
    top: calc(50% - 11px);
    margin-left: 0;
    margin-right: 0;
    left: -11px;
}

.Popup[data-placement^="right"] .Popup__arrow:after {
    border-width: 10px 10px 10px 0;
    border-left-color: transparent;
    border-top-color: transparent;
    border-bottom-color: transparent;
    top: calc(50% - 10px);
    margin-left: 0;
    margin-right: 0;
    left: 1px;
}

.Popup[data-placement^="left"] {
    margin-right: 10px;
}

.Popup[data-placement^="left"] .Popup__arrow {
    border-width: 11px 0 11px 11px;
    border-right-color: transparent;
    border-top-color: transparent;
    border-bottom-color: transparent;
    top: calc(50% - 11px);
    margin-left: 0;
    margin-right: 0;
    right: -11px;
}

.Popup[data-placement^="left"] .Popup__arrow:after {
    border-width: 10px 0 10px 10px;
    border-right-color: transparent;
    border-top-color: transparent;
    border-bottom-color: transparent;
    top: calc(50% - 10px);
    margin-left: 0;
    margin-right: 0;
    right: 1px;
}
