:root {
    --Link-text-color: var(--text-color--default);
    --Link-font-size: var(--font-size--regular);
    --Link--state-text-color: var(--text-color--primary);
    --Link--focus-box-shadow-width: var(--shadow-size--forState);
    --Link--focus-box-shadow-color: var(--shadow-color--focus);
}

.Link {
    font-size: var(--Link-font-size);
    color: var(--Link-text-color);
    text-decoration: underline;
    cursor: pointer;
}

.Link:hover,
.Link:focus,
.Link.is-active,
.Link:active {
    color: var(--Link--state-text-color);
    outline: 0;
}

.Link:focus:not(:active) {
    box-shadow: var(--Link--focus-box-shadow-width) var(--Link--focus-box-shadow-color);
}
