/**
 * LIST COMPONENT
 */

:root {
    --List-text-color: var(--text-color--default);
    --List-font-size: var(--font-size--regular);

    --List__item-padding: var(--space-size--sm) var(--space-size--md);

    --List__divider-border-color: var(--border-color--light);
    --List__divider-border-style: var(--border-style--default);
    --List__divider-border-width: var(--border-width--default);

    --List--small-font-size: var(--font-size--regular);
    --List--small__item-padding: var(--space-size--xs) var(--space-size--md);
    --List--large-font-size: var(--font-size--large);
    --List--large__item-padding: 0.875rem var(--space-size--md);
}

.List {
    margin: 0;
    padding: 0;
    color: var(--List-text-color);
    font-size: var(--List-font-size);
    list-style: none;
    line-height: 1;
}

.List__item {
    position: relative;
    padding: var(--List__item-padding);
}

.List.has-divider > .List__item,
.List__item.has-divider {
    border-top: var(--List__divider-border-width) var(--List__divider-border-style) var(--List__divider-border-color);
}

.List.has-divider > .List__item:first-child {
    border-top: none;
}

.List--horizontal.has-divider > .List__item,
.List--horizontal .List__item.has-divider {
    border-top: 0;
    border-left: var(--List__divider-border-width) var(--List__divider-border-style) var(--List__divider-border-color);
}

.List--horizontal.has-divider > .List__item:first-child {
    border-left: none;
}

.List__item.has-sublist {
    padding: 0;
}

.List .List {
    margin-left: 2em;
}

[dir=rtl] .List .List {
    margin-left: 0;
    margin-right: 2em;
}

.List + .List {
    margin-top: 1em;
}

.List--horizontal {
    font-size: 0;
}

.List--horizontal .List__item {
    display: inline-block;
    font-size: var(--List-font-size);
}

.List--horizontal .MediaObject__content {
    float: left;
}

.List {
    &--small {
        font-size: var(--List--small-font-size);

        & .List__item {
            padding: var(--List--small__item-padding);
        }
    }

    &--large {
        font-size: var(--List--large-font-size);

        & .List__item {
            padding: var(--List--large__item-padding);
        }
    }
}
