/** @define Drawer */

/**
 * A Drawer is a panel that slides in and out from the right.
 */

:root {
    --Drawer__container-background-color: var(--Background-color-lightest);
    --Drawer__container-box-shadow: rgba(0, 0, 0, 0.15);
    --Drawer__footer-background-color: var(--Whitelabel-color-light);
    --Drawer__footer-border-color: #e3e8ec;
    --Drawer__footer-padding: 0.75rem 1rem;

    --Drawer__content-padding: 0.75rem 1rem;
    --Drawer__header-padding: 0.75rem 1rem;
    --Drawer-padding: 0.75rem 1rem;

    --Drawer-border-color: #e3e8ec;

    --Drawer__container-font-size: 0.875rem;
    --Drawer__title-text-color: #59626a;
    --Drawer__close-color: #79848e;

    --Drawer__backdrop-background-color: rgba(0, 0, 0, 0.5);

    --Drawer-z-index: var(--z-index--Drawer);
}

.Drawer {
    box-sizing: border-box;
    z-index: var(--Drawer-z-index);
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    visibility: hidden;
    opacity: 0;

    transition: visibility 0 linear 0.35s, opacity 0.35s;
}

.Drawer.has-backdrop {
    background: var(--Drawer__backdrop-background-color);
}

.Drawer.is-open {
    visibility: visible;
    opacity: 1;

    transition-delay: 0s;
}

.Drawer__container {
    box-sizing: border-box;
    position: fixed;
    right: 0;
    top: 0;
    bottom: 0;
    height: 100%;
    width: 100%;
    font-size: var(--Drawer__container-font-size);
    background: var(--Drawer__container-background-color);
    box-shadow: 0 4px 14px 0 var(--Drawer__container-box-shadow);

    transform: translate3d(150%, 0, 0);

    transition: transform 0.35s, width 0.35s;
    transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
}

.Drawer.is-open .Drawer__container {
    transform: translate3d(0, 0, 0);
}

.Drawer__header {
    position: relative;
    padding: var(--Drawer__header-padding);
}

.Drawer__close {
    z-index: 9;
    position: absolute;
    top: 0.75rem;
    right: 0.75rem;
    cursor: pointer;
    color: var(--Drawer__close-color);
}

.Drawer__close svg {
    fill: currentColor;
    vertical-align: middle;
}

.Drawer__title {
    position: relative;
    margin: 0;
    font-weight: 400;
    display: block;
    font-size: 1.375rem;
    color: var(--Drawer-title-text-color);
}

.Drawer__content {
    position: relative;
    display: block;
    padding: var(--Drawer__content-padding);
    margin: 0;
    height: 100%;
    overflow-y: auto;
}

.Drawer__footer {
    position: relative;
    display: table-row;
    padding: var(--Drawer__footer-padding);
    background: color(var(--Drawer__footer-background-color) l(95%));
    min-height: 1rem;
    right: 0;
    left: 0;
    border-top: 1px solid var(--Drawer__footer-border-color);
}

@media (min-width: 40em) {
    .Drawer__container {
        width: 35em;
    }
}
