:root {
    --MediaObject-font-size: var(--font-size--regular);
    --MediaObject--small-font-size: var(--font-size--regular);
    --MediaObject--large-font-size: var(--font-size--large);

    --MediaObject-text-color: var(--text-color--default);

    --MediaObject__media-margin: var(--space-size--md);
    --MediaObject--small__media-margin: var(--space-size--sm);
    --MediaObject--large__media-margin: var(--space-size--lg);

    --MediaObject__content-font-size: var(--font-size--regular);
    --MediaObject--small__content-font-size: var(--font-size--small);
    --MediaObject--large__content-font-size: var(--font-size--large);
}

.MediaObject {
    overflow: hidden;
    font-size: var(--MediaObject-font-size);
    color: var(--MediaObject-text-color);
}

[dir=rtl] .MediaObject__media--right,
.MediaObject__media--left {
    float: left;
    margin-right: var(--MediaObject__media-margin);
}

[dir=rtl] .MediaObject__media--left,
.MediaObject__media--right {
    float: right;
    margin-left: var(--MediaObject__media-margin);
}

[dir=rtl] {
    .MediaObject--small .MediaObject__media--left,
    .MediaObject--large .MediaObject__media--left,
    .MediaObject__media--left {
        margin-right: 0;
    }

    .MediaObject--small .MediaObject__media--right,
    .MediaObject--large .MediaObject__media--right,
    .MediaObject__media--right {
        margin-left: 0;
    }
}

.MediaObject__image {
    width: 100%;
    height: auto;
}

.MediaObject__content {
    margin: 0;
    overflow: hidden;
    font-size: var(--MediaObject__content-font-size);
}

.MediaObject {
    &--small {
        [dir=rtl] & .MediaObject__media--right,
        & .MediaObject__media--left {
            margin-right: var(--MediaObject--small__media-margin);
        }

        [dir=rtl] & .MediaObject__media--left,
        & .MediaObject__media--right {
            margin-left: var(--MediaObject--small__media-margin);
        }

        & .MediaObject__content {
            font-size: var(--MediaObject--small__content-font-size);
        }
    }

    &--large {
        [dir=rtl] & .MediaObject__media--right,
        & .MediaObject__media--left {
            margin-right: var(--MediaObject--large__media-margin);
        }

        [dir=rtl] & .MediaObject__media--left,
        & .MediaObject__media--right {
            margin-left: var(--MediaObject--large__media-margin);
        }

        & > .MediaObject__content {
            font-size: var(--MediaObject--large__content-font-size);
        }
    }
}

