:root {
    --Pagination__item-border-color: var(--Border-color-light);
    --Pagination-color: var(--Text-color-light);
}

.Pagination {
    margin: 0;
    padding: 0;

    &__item {
        border-right: 1px solid var(--Pagination__item-border-color);
        list-style: none;
        padding: 0 0.5em 0 0.5em;

        &:last-child {
            border: none;
        }

        & a {
            color: var(--Pagination-color);
        }
    }
}
