/** @define Divider */

/**
 * A divider visually segments content into sections.
 */

:root {
    --Divider-height: var(--border-width--default);
    --Divider--large-height: 2px;
    --Divider-color: var(--background-color--secondary);
    --Divider-margin: var(--space-size--sm);
    --Divider--spacious-margin: var(--space-size--lg);
}

.Divider {
    display: block;
    margin: var(--Divider-margin) 0;
    border: 0;
    background: var(--Divider-color);
    overflow: hidden;
    height: var(--Divider-height);

    &--large {
        height: var(--Divider--large-height);
    }

    &--compact {
        margin: 0;
    }

    &--spacious {
        margin-top: var(--Divider--spacious-margin);
        margin-bottom: var(--Divider--spacious-margin);
    }
}
