:root {
    --Overlay-background-color: var(--Background-color-darkest);
    --Overlay--light-background-color: var(--Background-color-light);
    --Overlay-color: #ddd;
    --Overlay-z-index: var(--z-index--Overlay);
}

.Overlay {
    z-index: var(--Overlay-z-index);
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    background: var(--Overlay-background-color);
    color: var(--Overlay-color);

    opacity: 0;
    display: block;
    visibility: hidden;
    transition: visibility 0s linear 0.35s, opacity 0.5s;

    &.is-active {
        visibility: visible;
        transition-delay: 0s;
        opacity: 0.75;
    }

    &--limited {
        position: absolute;
    }

    &--light {
        color: inherit;
        background: var(--Overlay--light-background-color);
    }

    .Overlay__content {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}
