:root {
    --Fieldset__legend-color: #444;
    --Fieldset__legent-font-size: 20px;
}

.Fieldset {
    border: 0;
    padding: 22px 0 0 0;

    legend {
        font-size: var(--Fieldset__legent-font-size);
        letter-spacing: 1px;
        color: var(--Fieldset__legend-color);
    }
}

* + .Fieldset {
    margin-top: 44px;
}
